export default class MoveImg {
  ctx: CanvasRenderingContext2D
  canvasWidth = 0
  canvasHeight = 0
  constructor (ctx: CanvasRenderingContext2D) {
    this.ctx = ctx
    this.canvasWidth = ctx.canvas.width
    this.canvasHeight = ctx.canvas.height
  }

  moveImage (startX: number, startY: number): void {
    const img = this.getImage()
    const x = startX
    const y = startY
    const source = this.ctx.getImageData(x, y, 32, 32)
    this.ctx.drawImage(img, x, y, 32, 32)
    /* this.ctx.drawImage(img, x, x, 32, 32) */
    /* const interval = setInterval(() => {
      if (source) {
        this.ctx.clearRect(x, y, 32, 32)
        this.ctx.putImageData(source, x, y)
        x += 0
        y++
        source = this.ctx.getImageData(x, y, 32, 32)
        this.ctx.drawImage(img, x, y, 32, 32)
        if (x > this.canvasWidth || y > this.canvasHeight) {
          clearInterval(interval)
        }
      }
    }, 20) */
  }

  private getImage (): HTMLImageElement {
    return document.getElementById('img') as HTMLImageElement
  }
}
