import HeartPoint from '@/js/HeartPoint'
import MoveImg from '@/js/MoveImg'
import Flowers from '@/js/Flowers'

export default class DrawHeartCanvas {
  ctx: CanvasRenderingContext2D
  canvasWidth = 0
  canvasHeight = 0

  constructor (ctx: CanvasRenderingContext2D) {
    this.ctx = ctx
    this.canvasWidth = ctx.canvas.width
    this.canvasHeight = ctx.canvas.height
  }

  drawHeart (): void {
    const oneFifth = this.canvasHeight / 2.3
    const sPointX = this.canvasWidth >> 1 // 心的起点坐标
    const sPointY = oneFifth
    const {
      right,
      left
    } = this.getHeartAllPoint()
    this.ctx.moveTo(right[0].x + sPointX, right[0].y)
    console.log(right.length + '---' + left.length)
    let r = 0
    let intervalR = 0
    const flowers = new Flowers(this.ctx)
    flowers.draw()
    clearInterval(intervalR)
    intervalR = setInterval(() => {
      if (r < right.length) {
        const index = right[r]
        if (r % 3 === 0) {
          flowers.addFlower(index.x + sPointX, index.y + sPointY)
        }
      } else {
        clearInterval(intervalR)
      }
      r++
    }, 20)

    let l = 0
    let intervalL = 0
    clearInterval(intervalL)
    intervalL = setInterval(() => {
      if (l < left.length) {
        const index = left[l]
        if (r % 3 === 0) {
          flowers.addFlower(index.x + sPointX, index.y + sPointY)
        }
      } else {
        clearInterval(intervalL)
      }
      l++
    }, 20)
  }

  private sleep (delay: any) {
    for (let t = Date.now(); Date.now() - t <= delay;) { }
  }

  /**
   * 绘制动态心到大心里面
   * 之后向上飘动
   * @private
   */
  private drawDynamicHeart (right: Array<HeartPoint>, left: Array<HeartPoint>): void {
    const arrRX: number[] = []
    const arrRY: number[] = []
    right.forEach((v, i, a) => {
      arrRX.push(v.x)
      arrRY.push(v.y)
    })
    left.forEach((v, i, a) => {
      arrRX.push(v.x)
      arrRY.push(v.y)
    })
    setInterval(() => {
      const rX = this.randomNumberInRange(0, right.length)
      const rX2 = this.randomNumberInRange(0, right.length)
      const drRX = right[Math.floor(rX)].x
      const drRY = right[Math.floor(rX)].y - 15

      const drRX2 = right[Math.floor(rX2)].x
      const drRY2 = right[Math.floor(rX2)].y - 15
      const move = new MoveImg(this.ctx)
      move.moveImage(
        this.canvasWidth / 2 + this.randomNumberInRange(Math.min(drRX2, drRX), Math.max(drRX2, drRX)),
        this.canvasHeight / 2.3 + this.randomNumberInRange(Math.min(drRY2, drRY), Math.max(drRY2, drRY))
      )
      this.ctx.stroke()
    }, 10)
  }

  /**
   * 获取范围内的随机数
   * @param m
   * @param n
   * @private
   */
  private randomNumberInRange (min: any, max: any): number {
    return Math.random() * (max - min) + min
  }

  /**
   * 获取左右半边心的每个点的坐标
   * @private
   */
  private getHeartAllPoint (): { right: Array<HeartPoint>, left: Array<HeartPoint> } {
    let radian = 0
    const radianAdd = Math.PI / 180
    const arrRight = []
    const arrLeft = []
    while (radian <= (Math.PI * 2)) {
      radian += radianAdd
      const x = this.getHeartX(radian)
      const y = this.getHeartY(radian)
      console.log(x + '----' + y)
      if (x > 0) {
        arrRight.push(new HeartPoint(x, y))
      } else {
        arrLeft.push(new HeartPoint(x, y))
      }
    }
    return {
      right: arrRight,
      left: arrLeft
    }
  }

  private getHeartX (t: any): number {
    return (this.canvasHeight / 35) * (16 * Math.pow(Math.sin(t), 3))
  }

  private getHeartY (t: any) { // 获取心型线的Y坐标
    return -(this.canvasHeight / 35) * (13 * Math.cos(t) - 5 * Math.cos(2 * t) - 2 * Math.cos(3 * t) - Math.cos(4 * t))
  }
}
