
class FsData {
  size: number
  petals: number
  rot: number
  c: number[]
  x: number
  y: number
  constructor (size: number, petals: number, rot: number, c: number[], x: number, y: number) {
    this.size = size
    this.petals = petals
    this.rot = rot
    this.c = c
    this.x = x
    this.y = y
  }
}

export default class Flowers {
  ctx: CanvasRenderingContext2D
  w
  h
  fps = 30
  interval = 1000 / this.fps
  frame: number
  sf
  grd: CanvasGradient
  numFlowers = 7
  fs = new Array<FsData>()
  constructor (ctx: CanvasRenderingContext2D) {
    this.ctx = ctx
    this.w = ctx.canvas.width
    this.h = ctx.canvas.height
    this.frame = 0
    this.sf = Math.min(this.w, this.h) / 6 // scale factor
    this.grd = ctx.createRadialGradient(this.w / 2, this.h / 2, 0, this.w / 2, this.h / 2, this.sf * 1.2)
    this.grd.addColorStop(0, 'rgba(255,255,255,.25)')
    this.grd.addColorStop(1, this.cb(this.rand(200, 255), this.rand(200, 255), this.rand(200, 255), 25))
    /* for (let i = 0; i < this.numFlowers; i++) {
      this.fs.push({
        size: this.rand(1, 10),
        petals: this.rand(4, 12),
        rot: this.rand(-5, 5),
        c: [this.rand(100, 255), this.rand(100, 255), this.rand(100, 255)] as never,
        x: (this.w / 2) as never,
        y: this.h / 2 as never
      } as never)
    } */

    this.fs.sort((fd) => {
      return parseFloat(fd.size + '') - parseFloat(fd.size + '')
    })
  }

  private cb (r: number, g: number, b: number, o: number): string {
    return 'rgba(' + r + ',' + g + ',' + b + ',' + o + ')'
  }

  private rand (min: number, max: number): number {
    return Math.round(Math.random() * (max - min) + min)
  }

  private P2L (r: number, angle: number) {
    const ret = {
      x: 0,
      y: 0
    }
    ret.x = Math.cos(angle * Math.PI / 180) * r
    ret.y = Math.sin(angle * Math.PI / 180) * r
    return (ret)
  }

  private drawFlower (rad: number, numPts: number, initAngle: number, spinVel: number, c: number[], _x: number, _y: number) {
    // this.ctx.shadowBlur = 50
    this.ctx.lineWidth = 1
    this.ctx.shadowColor = this.cb(c[0], c[1], c[2], 1)
    this.ctx.fillStyle = this.cb(c[0], c[1], c[2], 0.9)
    const c2 = [Math.floor(c[0] / 1.6), Math.floor(c[1] / 1.6), Math.floor(c[2] / 1.6)]

    this.ctx.strokeStyle = this.cb(c2[0], c2[1], c2[2], 1)

    const pts = []
    for (let i = 0; i <= numPts; i++) {
      const _a = (360 / numPts) * i + initAngle + this.frame * spinVel

      pts.push({ x: this.P2L(rad, _a).x, y: this.P2L(rad, _a).y })
    }

    for (let i1 = 1; i1 <= numPts; i1 += 2) {
      const idx = i1 % numPts
      this.ctx.beginPath()
      this.ctx.moveTo(_x, _y)
      this.ctx.bezierCurveTo(_x + pts[i1 - 1].x, _y + pts[i1 - 1].y, _x + pts[idx + 1].x, _y + pts[idx + 1].y, _x, _y)
      this.ctx.stroke()
      this.ctx.fill()
    }
  }

  addFlower (_x: number, _y: number) {
    this.fs.push(new FsData(
      this.rand(1, 5),
      this.rand(4, 12),
      this.rand(-15, 15),
      [this.rand(100, 255), this.rand(100, 255), this.rand(100, 255)],
      _x,
      _y
    ))
    // this.numFlowers++
  }

  draw (): void {
    setTimeout(() => {
      this.frame += 2
      // clear canvas
      this.ctx.clearRect(0, 0, this.w, this.h)
      this.ctx.shadowBlur = 0
      this.ctx.fillStyle = this.grd
      this.ctx.fillRect(0, 0, this.w, this.h)
      for (let i = 0; i < this.fs.length; i++) {
        this.drawFlower(this.sf * this.fs[i].size / 10,
          this.fs[i].petals * 2, 0, this.fs[i].rot / 15,
          this.fs[i].c, this.fs[i].x, this.fs[i].y)
      }
      window.requestAnimationFrame(() => {
        this.draw()
      })
    }, this.interval)
  }
}
